.footer {
  width: 100%;
  height: 400px;
  z-index: 888;
  background: #202020;
  @media (max-width: 768px) {
    height: fit-content;
    z-index: 888;
    padding: 40px 20px;
    background: #1e1e1e;
    > div {
      display: flex;
      flex-direction: column;
      color: #fff;
      .bold-link {
        font-weight: bold;
        font-size: 16px;
      }
      a {
        color: #fff;
        line-height: 28px;
        &:hover {
          color: #ec407a;
        }
      }
      .copy {
        &::before {
          width: 100%;
          height: 1px;
          content: '';
          display: block;
          margin-bottom: 30px;
          background: rgba($color: #fff, $alpha: 0.35);
        }
      }
    }
  }
  .container {
    width: 1140px;
    margin: 0 auto;
    padding-top: 97px;
    display: flex;
    justify-content: space-between;

    .logo {
      width: 172px;
      height: 26px;
      background: url(../../assets/imgs/imgs/logo_white.svg) no-repeat center
        center;
      background-size: 100% 100%;
      margin-bottom: 20px;
    }

    .block {
      &:last-child {
        width: 360px;
      }

      .title {
        margin-bottom: 10px;
        width: 165px;
        height: 25px;
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        color: white;
        margin-bottom: 20px;
        cursor: pointer;
        &:hover {
          color: #ec407a;
        }

        a {
          color: white;
          &:hover {
            color: #ec407a;
          }
        }
      }

      .text {
        display: block;
        width: 165px;
        height: 25px;
        line-height: 20px;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        color: #f0f0f0;
        white-space: nowrap;
        word-break: keep-all;
        cursor: pointer;

        &:hover {
          color: #ec407a;
        }

        &:first-child {
          @extend .title;
        }
      }
    }

    .icon {
      width: 24px;
      height: 24px;
      &:hover {
        opacity: 0.8;
      }
      ~ .icon {
        margin-left: 18px;
      }
    }
  }

  @media (max-width: 768px) {
    height: auto;

    .container {
      width: 92%;

      .links-container {
        display: block;
        height: auto;
        padding: 20px 0;

        .logo {
          width: 138px;
          height: 20px;
          background: url(../../assets/imgs/imgs/logo_white.png) no-repeat
            center center;
          background-size: 100% 100%;
        }

        .links {
          margin: 15px 0 0;

          .link-item {
            margin: 0 30px 0 0;
            font-size: 14px;
            line-height: 30px;
          }
        }
      }

      .copyright {
        height: 83px;
        justify-content: flex-start;
      }
    }
  }
}
