.top {
  margin: 155px auto 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .left-img {
    margin-right: 35px;
  }
  .right-img {
    margin-left: 35px;
  }
  @media (max-width: 768px) {
    margin: 60px auto 0;
    padding: 0 20px;
    > img {
      width: 30px;
    }
    .left-img {
      margin-right: 20px;
    }
    .right-img {
      margin-left: 20px;
    }
  }
  .centertext {
    font-style: normal;
    font-weight: 700;
    line-height: 112px;
    letter-spacing: -1px;
    text-align: center;
    color: #1e1e1e;
    @media (max-width: 768px) {
      width: 255px;
      line-height: 48px;
    }
  }
}
.toptext {
  width: 644px;
  height: fit-content;
  margin: 30px auto 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0px;
  text-align: center;
  color: #1e1e1e;
  @media (max-width: 768px) {
    width: 255px !important;
    height: fit-content;
    text-align: center;
    word-break: break-word;
  }
}
