@font-face {
  font-family: 'IBM Plex Sans';
  src: url('./assets/font/IBMPlexSans-Regular.ttf');
}

blockquote,
body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
input,
legend,
li,
ol,
p,
pre,
td,
textarea,
th,
div,
ul {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  margin: 0;
  background: #fff;
  color: #3f3f3f;
}

body,
button,
input,
select,
textarea {
  font: 14px 'IBM Plex Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}

address,
cite,
dfn,
em,
var {
  font-style: normal;
}

code,
kbd,
pre,
samp {
  font-family: courier new, courier, monospace;
}

small {
  font-size: 12px;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none !important;
  color: #000;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;

  &:hover {
    text-decoration: underline;
  }
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

legend {
  color: #000;
}

fieldset,
img {
  border: 0;
}

button,
input,
select,
textarea {
  font-size: 100%;
  outline: none;
  border: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select {
  border: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
  outline: none;
  background-color: transparent;
  vertical-align: middle;
}

.font-bold {
  font-weight: bold;
}

.font-42 {
  font-size: 42px;
  line-height: 50px;
}

.font-32 {
  font-size: 32px;
  line-height: 40px;
}

.font-24 {
  font-size: 24px;
  line-height: 32px;
}

.font-20 {
  font-size: 20px;
  line-height: 35px;
}

.font-16 {
  font-size: 16px;
  line-height: 25px;
}

.grey-color {
  color: #6f6f6f;
}

.blue-color {
  color: #0033cc;
}
.white-c {
  color: white;
}

.default-container {
  overflow: hidden;
}

.text_content {
  width: 100%;
  font-size: 20px;
  line-height: 25px;
  word-break: break-word;
  margin: auto;

  @media screen and (max-width: 768px) {
    text-align: left !important;
  }
}

.text_content > div {
  margin-bottom: 30px;
}

.text_content > div :last-child {
  margin-bottom: 0;
}

button {
  text-align: center;
  vertical-align: middle;
  font-size: 14px;
  font-weight: bold !important;
  color: #fff;
  background: #ec407a;
  cursor: pointer;
  transition: opacity 0.5s ease-in-out;
}

button:hover {
  opacity: 0.8;
}
