.menu {
  width: 100%;
  height: 85px;
  background: #ffffff;
  position: fixed;
  top: 0;
  z-index: 1000;
  box-shadow: 0 5px 10px 5px #e5e5e549;

  .container {
    @media screen and (max-width: 768px) {
      width: 100%;
      padding: 0 18px;
    }
    width: 1140px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo {
      width: 172px;
      height: 26px;
      background: url(../../assets/imgs/imgs/logo.png) no-repeat center center;
      background-size: 100% 100%;
    }

    .menu-links {
      height: 30px;
      line-height: 30px;
      display: flex;
      align-items: center;
      .links {
        .link-item {
          display: inline-block;
          margin: 0 20px;
          &:nth-child(3) {
            margin-right: 0px;
          }
          &:last-child {
            margin: 0 40px;
          }
          .link-element {
            display: block;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            color: #000000;
            transition: all 0.3s;
            border-bottom: 2px solid #fff;
            &:hover {
              color: #ec407a;
              border-color: #ec407a;
            }

            &.active {
              color: #ec407a;
              border-color: #ec407a;
            }
          }
        }
      }

      .contact-us {
        padding: 8px 36px;
        background: #ec407a;
        border-radius: 50px;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        text-align: center;
        color: #f5f9fc;
        margin-left: 55px;
      }
    }
  }

  @media (max-width: 768px) {
    height: auto;
    min-height: 60px;
    display: flex;
    align-items: center;

    .container {
      width: 92%;
      margin: 0 auto;
      padding: 0;

      .logo {
        width: 138px;
        height: 20px;
        background: url(../../assets/imgs/imgs/logo.png) no-repeat center center;
        background-size: 100% 100%;
      }

      .menu-trigger {
        width: 16px;
        height: 16px;
        background: url(../../assets/imgs/mobile/menu_m.png) no-repeat center;
        background-size: 100% 100%;
      }
    }

    .menu-fillscreen {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: #fff;
      z-index: 99999;
      padding: 20px 15px;

      .close-menu {
        width: 24px;
        height: 24px;
        background: url(../../assets/imgs/mobile/close_m.png) no-repeat center;
        background-size: 100% 100%;
        position: absolute;
        right: 20px;
        top: 20px;
      }

      .links {
        margin: 50px 0 0;
        padding: 0 20px;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 25px;

        .link-item {
          margin: 0 0 20px;
          .link-element {
            font-weight: bold;
            color: #1e1e1e;
            &:hover {
              color: #ec407a;
            }

            &.active {
              color: #ec407a;
            }
          }
        }
      }
    }
  }
}

.ant-dropdown-menu {
  background: #fff;
  padding: 0 !important;
  border-radius: 8px !important;

  .ant-dropdown-menu-item {
    padding: 10px 30px;

    &:first-child {
      border-top-left-radius: 8px !important;
      border-top-right-radius: 8px !important;
    }

    &:last-child {
      border-bottom-left-radius: 8px !important;
      border-bottom-right-radius: 8px !important;
    }

    &:hover {
      background-color: #ec407a;

      a {
        color: #fff !important;
      }
    }
  }
}
