.imgBg {
  height: 520px;
  // background: url(../../assets/imgs/imgs/Rectangle.png) center / cover no-repeat;
  @media (max-width: 768px) {
    height: fit-content;
  }
  .bgContent {
    width: 1140px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    @media (max-width: 768px) {
      width: 100% !important;
      word-break: break-word;
      padding: 0 20px;
      top: auto !important;
      transform: translateY(0) !important;
    }
  }
}
