.home-banner {
  position: relative;
  width: 830px;
  padding-top: 85px;
  margin: auto auto 70px auto;
  @media (max-width: 768px) {
    width: 100%;
    word-break: break-word;
  }
  div {
    position: relative;
    z-index: 99;
  }

  .bg {
    position: absolute;
    width: 650px;
    height: calc(100% - 15px);
    right: 0;
    bottom: 0;
    background: url('../../assets/imgs/imgs/home_banner.png') center / cover
      no-repeat;
    z-index: 33;
  }
  .text-1 {
    font-size: 84px;
    font-weight: bold;
    line-height: 100px;
    color: #ec407a;
  }
  .text-2 {
    font-size: 17px;
    line-height: 25px;
    color: #3f3f3f;
  }
  .text-3 {
    font-size: 20px;
    line-height: 28px;
    color: #6f6f6f;
  }
  .flex {
    display: flex;
  }
}
.banner {
  width: 100%;
  padding-top: 85px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 25px;
  line-height: 35px;
  margin-bottom: 70px;
  > div {
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  .title {
    font-size: 55px;
    text-align: center;
    font-weight: bold;
    line-height: 60px;
  }
}

.mobile-banner-wrap {
  width: 100%;
  padding: 50px 15px 45px;
  word-break: break-word;
  margin-bottom: 70px;
  > div {
    &:first-child {
      font-size: 44px;
      line-height: 52px;
      font-weight: bold;
      margin-bottom: 15px;
    }
  }
  button {
    width: 100%;
    height: 40px;
    border-radius: 50px;
    margin-top: 90px;
  }
}

.mobile-banner-bg {
  width: 100%;
  height: 390px;
}
